.ColorBox {
	width: 20%;
	height: 25%;
	margin: 0 auto;
	display: inline-block;
	position: relative;
	cursor: pointer;
	margin-bottom: -4px;
}

.SingleColorPalette .ColorBox {
	height: 50%;
}

/* .ColorBox .go-back {
  background-color: yellowgreen;
} */

.go-back {
	background-color: black;
}

.copy-button,
.back-button {
	width: 40px;
	height: 30px;
	position: absolute;
	display: inline-block;
	top: 50%;
	left: 50%;
	margin-left: -20px;
	margin-top: -15px;
	text-align: center;
	outline: none;
	border: none;
	font-size: 1rem;
	line-height: 30px;
	color: white;
	text-transform: uppercase;
	background: #ced4da;
	opacity: 0;
	cursor: pointer;
	text-decoration: none;
}

.dark-text-forCopy {
	background-color: #a6a7ab;
	color: black;
}

.back-button {
	width: 100px;
	opacity: 1;
}

.ColorBox:hover .copy-button {
	transition: 0.5s;
	opacity: 0.5;
}

.ColorBox:active .copy-button {
	transition: 0.2s;
	color: black;
	transform: translateY(1rem);
}

.box-content {
	position: absolute;
	width: 100%;
	left: 0px;
	bottom: 0px;
	padding: 10px;
	color: black;
	letter-spacing: 1px;
	text-transform: uppercase;
	font-size: 12px;
}

.light-text {
	color: white;
}

.see-more {
	background: #ced4da;
	position: absolute;
	border: none;
	right: 0px;
	bottom: 0px;
	color: white;
	width: 60px;
	height: 30px;
	text-align: center;
	line-height: 30px;
	text-transform: uppercase;
	font-size: 12px;
	opacity: 0.9;
}

.dark-text {
	color: black;
}

.copy-overlay {
	opacity: 0;
	z-index: 0;
	width: 100%;
	height: 100%;
	transition: transform 0.6s ease-in-out;
	transform: scale(0.1);
}

.copy-overlay.show {
	opacity: 1;
	transform: scale(50);
	z-index: 10;
	position: absolute;
}

.copy-msg {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	font-size: 4rem;
	transform: scale(0.1);
	opacity: 0;
	color: white;
}

.copy-msg.show {
	opacity: 1;
	transform: scale(1);
	z-index: 25;
	transition: all 0.5s ease-in-out;
	transition-delay: 0.3s;
}

.copy-msg h1 {
	font-weight: 400;
	background: rgba(255, 255, 255, 0.2);
	width: 100%;
	text-align: center;
	margin-bottom: 0;
	padding: 1rem;
	text-transform: uppercase;
}

.copy-msg p {
	font-size: 2rem;
}

@media screen and (max-width: 900px) {
	.ColorBox {
		width: 50%;
	}
}

@media screen and (max-width: 575px) {
	.ColorBox {
		width: 100% !important;
		height: 5% !important;
	}

  .see-more {
    display: flex;
    height: 20px;
    font-size: 10px;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

}

@media screen and (max-width: 575px) {
  .SingleColorPalette .ColorBox {
    height: 10% !important;
  }  
}

